import type { Metadata } from "next";
import { notFound } from "next/navigation";

const ARTICLES = {
  "understanding-apr-calculations": {
    title: "Understanding APR calculations for short-term loans",
    summary:
      "APR translates flat fees into an annualized figure to standardize comparisons across loan types.",
    body: [
      "APR (annual percentage rate) transforms flat fees collected on a short-term loan into an annualized cost. Payday loans often quote a flat $15 fee per $100 borrowed, which equals a 391% APR for a 14-day term.",
      "Installment loans spread payments across months. Convert origination fees and interest into APR by using the federal Truth in Lending Act formula or a calculator.",
      "Always compare APR to your state cap and even consider alternative products like credit union PALs or employer-sponsored advances.",
    ],
  },
  "how-to-improve-pre-qualification-odds": {
    title: "Improve your pre-qualification odds",
    summary:
      "Small preparation steps help lenders verify your information quickly and present better offers.",
    body: [
      "Verify your personal details and gather the last two pay stubs or benefit statements before you submit a request.",
      "Make sure your bank account is in good standing with at least 90 days of history.",
      "Enable SMS or email alerts so you can respond to verification steps and avoid delays.",
    ],
  },
  "state-payday-alternatives": {
    title: "Payday alternatives available by state",
    summary:
      "Credit unions, community development financial institutions, and nonprofits offer regulated alternatives in many states.",
    body: [
      "Check with local credit unions for Payday Alternative Loans (PALs) offering capped fees and structured repayment.",
      "Many employers partner with earned wage access platforms—review the fees and whether they impact your paycheck.",
      "Nonprofit organizations and community assistance programs often provide small loans or grants for utilities and emergencies.",
    ],
  },
} as const;

type Slug = keyof typeof ARTICLES;

export function generateStaticParams() {
  return Object.keys(ARTICLES).map((slug) => ({ slug }));
}

export async function generateMetadata({ params }: { params: Promise<{ slug: string }> }): Promise<Metadata> {
  const { slug } = await params;
  const article = ARTICLES[slug as Slug];
  if (!article) return { title: "Guide not found" };
  return {
    title: `${article.title} | Loan AI Portal Learn`,
    description: article.summary,
  };
}

export default async function ArticlePage({ params }: { params: Promise<{ slug: string }> }) {
  const { slug } = await params;
  const article = ARTICLES[slug as Slug];
  if (!article) return notFound();

  return (
    <div className="mx-auto max-w-3xl space-y-8 px-4 py-16 lg:px-8">
      <header className="space-y-3">
        <h1 className="text-3xl font-semibold text-slate-900">{article.title}</h1>
        <p className="text-sm text-slate-500">{article.summary}</p>
      </header>
      <article className="prose prose-slate">
        {article.body.map((paragraph) => (
          <p key={paragraph}>{paragraph}</p>
        ))}
      </article>
    </div>
  );
}
